<template>
	<view class="py-2 border-bottom flex flex-wrap">
		<image v-for="(item,i) in imageList" @click="previewimage(item)" :key="i" :src="item"  style="width: 206.6rpx;height: 206.6rpx;" class="mr-1 mb-1 rounded"  mode=""></image>
		<view @click="$emit('chooseimage')" v-if="imageList.length<9" class="flex border   justify-center align-center rounded" style="width: 206.6rpx;height:206.6rpx;" >
			<view class="flex  justify-center align-center  px-2 ">
				<text style="font-size: 80rpx;" class="text-light-muted">+</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props:{
			imageList:{
				type:Array,
				default:()=>{[]}
			}
		},
		methods: {
			previewimage(item)
			{
				uni.previewImage({
					current:item,
					urls:this.imageList
				})
			}
		},
	}
</script>

